import React, { PureComponent } from 'react'
import Child from './Child'


export default class Parent extends PureComponent {
  state = {
    error:''
  }

  static getDerivedStateFromError(error) {
    console.log("$$$$$$$$$",error);
    return {error:error}
  }

  render() {
    return (
      <div>
        <h2>Parent组件</h2>
        {this.state.error ? <h4>网络出错，请稍后再试</h4> : <Child />}
      </div>
    )
  }
}
